import { Injectable } from '@angular/core';
import { Search, SearchFactory } from '@c8y/ngx-components';
import { BridgeService } from './bridge.service';

@Injectable()
export class Ng1SearchFactoryService implements SearchFactory {
  search: Search[];
  constructor(private bridge: BridgeService) {
    this.search = this.bridge.getSearch();
  }

  get() {
    return this.search;
  }
}
