import { Injectable } from '@angular/core';
import { ActionBarFactory, ActionBarItem } from '@c8y/ngx-components';
import { BridgeService } from './bridge.service';
import { Observable } from 'rxjs';

@Injectable()
export class Ng1ActionBarFactoryService implements ActionBarFactory {
  routeChanges$: Observable<any>;
  constructor(
    private bridge: BridgeService
  ) {
    this.routeChanges$ = bridge.$routeChanges;
  }

  get(): Observable<ActionBarItem> {
    return this.bridge.getActionBarItems();
  }
}
