import { Injectable } from '@angular/core';
import { DocLinkFactory, DocLink } from '@c8y/ngx-components';
import { BridgeService } from './bridge.service';

@Injectable()
export class Ng1DocsFactoryService implements DocLinkFactory {
  private links: Promise<DocLink[]>;
  constructor(private bridge: BridgeService) {
    this.links = this.bridge.getQuickLinks();
    this.links.then(list => {
      list.map(el => {
        el.type = el.type || 'quicklink';
        return el;
      });
    });
  }

  get() {
    return this.links;
  }
}
