import { Injectable } from '@angular/core';
import { BreadcrumbFactory } from '@c8y/ngx-components';
import { ReplaySubject } from 'rxjs';
import { debounceTime, switchMap } from 'rxjs/operators';
import { BridgeService } from './bridge.service';

@Injectable()
export class Ng1BreadcrumbFactoryService implements BreadcrumbFactory {
  private trigger: any = new ReplaySubject(1);
  private breadcrumbs = this.trigger.pipe(
    debounceTime(100),
    switchMap(() => {
      return this.bridge.getBreadcrumbs();
    })
  );
  constructor(
    private bridge: BridgeService
  ) {}

  get() {
    this.trigger.next();
    return this.breadcrumbs;
  }
}
