import { Input, ChangeDetectionStrategy, Component, ElementRef, OnInit, OnDestroy } from '@angular/core';
import { DashboardBridgeService } from './dashboard-bridge.service';

@Component({
  selector: 'c8y-device-selector-legacy',
  template: '',
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class DeviceSelectorComponent implements OnInit, OnDestroy {
  ng1Scope;
  config;

  constructor(
    private dashboardBridgeService: DashboardBridgeService,
    private el: ElementRef
  ) {}

  async ngOnInit() {
    this.ng1Scope = this.dashboardBridgeService.instantiateDeviceSelector(this.el.nativeElement, this.config);
  }

  ngOnDestroy(): void {
    if (this.ng1Scope) {
      this.ng1Scope.$destroy();
    }
  }
}
