import { Injectable } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { ActionBarFactory, ActionBarItem, ViewContext } from '@c8y/ngx-components';
import { CopyActionComponent } from './copy-action.component';
import { DashboardBridgeService } from './dashboard-bridge.service';
import { PasteActionComponent } from './paste-action.component';

@Injectable()
export class Ng1DashboardActionFactoryService implements ActionBarFactory {
  constructor(private dashboardBridgeService: DashboardBridgeService) {}

  get(activeRoute?: ActivatedRoute) {
    const actions = [];
    const data =
      !activeRoute.parent || activeRoute.snapshot.data.context
        ? activeRoute.snapshot.data
        : activeRoute.parent.snapshot.data;
    const { contextData } = data;
    const isDashboard = contextData && activeRoute.snapshot.data.dashboard;
    const canPaste =
      contextData &&
      [ViewContext.Device, ViewContext.Group].includes(data.context) &&
      this.dashboardBridgeService.dashboardClipboard;

    if (isDashboard) {
      actions.push({
        priority: -20,
        placement: 'more',
        template: CopyActionComponent
      } as ActionBarItem);
    }

    if (canPaste) {
      actions.push({
        priority: -10,
        placement: 'more',
        template: PasteActionComponent
      } as ActionBarItem);
    }

    return actions;
  }
}
